
body {
  font-family: Arial, "Microsoft YaHei";
  color: #fff;
}
li {
  list-style-type: none;
}
.box {
  width: 400px;
  margin: 50px auto 0;
}
.puzzle-wrap {
  width: 410px;
  height: 410px;
  margin-bottom: 40px;
  padding: 0;
  background: #ccc;
  list-style: none;
}
.puzzle {
  float: left;
  width: 100px;
  height: 100px;
  font-size: 20px;
  background: #37c;
  text-align: center;
  line-height: 100px;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 4px;
  cursor: pointer;
}
.puzzle-empty {
  background: #ccc;
  box-shadow: inset 2px 2px 18px;
}
.puzzle-empty img {
  display: none;
}
.btn-reset {
  width: 410px;
  height: 40px;
  background: #37c;
  border: none;
  color: #fff;
  outline: none;
}


